.contact-container {
    width: 300px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    text-align: center;
    background-color: #f9f9f9;
  }
  
  .contact-info {
    text-align: left;
  }
  
  p {
    margin: 10px 0;
    font-size: 16px;
  }
  
  strong {
    color: #333;
  }
  