/* General styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f8f9fa;
}

/* Navigation */
.ulist {
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: #93C6E7;
  display: flex;
  width: 100vw; /* Full width */
  position: fixed; /* Keeps it at the top */
  top: 0;
  left: 0;
  z-index: 1000; /* Ensures it stays above other content */
}

/* Adjust body to prevent content from hiding behind the navbar */
.container {
  padding-top: 60px; /* Push content down so it's not hidden under the navbar */
  
}

/* Navbar items */
.ulist li {
  flex: 1; /* Each item takes equal width */
  text-align: center;
}

/* Links */
.links {
  text-decoration: none;
  color: white;
  font-size: 18px;
  font-weight: bold;
  display: block;
  padding: 15px 0;
  transition: 0.3s ease-in-out;
  width: 100%;
}

.links:hover {
  background-color: #6096B4;
}
