body {
  font-family: "Poppins", sans-serif;
  margin: 0; /* Remove margin to prevent unwanted space */
  padding: 0;
  background-color: #faf9f6;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main {
  flex: 1;
  margin-top: 80px; /* Pushes content below navbar */
  padding: 20px;
}

/* Center the heading */
h1 {
  font-size: 3rem;
  margin-bottom: 20px;
  text-align: left;
  padding-left: 50px;
}

/* Footer */
footer {
  text-align: right;
  color: midnightblue;
  margin-right: 20px;
}

/* Ensure images and text are aligned */
.card {
  width: 45%;
  display: inline-block;
  vertical-align: top;
  margin: 10px;
  text-align: left;
}

.tile-image {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.creative {
  color: midnightblue;
}
