/* General Styles */
body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
  }
  
  /* Form Container */
  .form {
    background-color: #fff;
    padding: 25px;
    border-radius: 12px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
    width: 350px;
    text-align: center;
    transition: transform 0.3s ease-in-out;
  }
  
  .form:hover {
    transform: scale(1.02);
  }
  
  /* Form Title */
  .form h1 {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 15px;
    color: #333;
  }
  
  /* Input Fields */
  .input {
    width: 100%;
    padding: 12px;
    margin-top: 5px;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 16px;
    transition: border-color 0.3s, box-shadow 0.3s;
  }
  
  .input:focus {
    border-color: #28a745;
    outline: none;
    box-shadow: 0px 0px 5px rgba(40, 167, 69, 0.5);
  }
  
  /* Labels */
  .label {
    font-weight: bold;
    text-align: left;
    display: block;
    margin-top: 12px;
    font-size: 14px;
    color: #333;
  }
  
  /* Name Field in One Row */
  .name-group {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 12px;
  }
  
  .name-group .label {
    margin-right: 10px;
    font-size: 15px;
    font-weight: bold;
    white-space: nowrap;
  }
  
  .name-group .input {
    flex: 1;
  }
  
  /* Submit Button */
  .btn {
    background-color: #28a745;
    color: white;
    border: none;
    padding: 12px;
    width: 100%;
    font-size: 17px;
    font-weight: bold;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    margin-top: 15px;
  }
  
  .btn:hover {
    background-color: #218838;
    transform: scale(1.05);
  }
  
  /* Success & Error Messages */
  .success {
    color: green;
    font-weight: bold;
    font-size: 16px;
    margin: 12px 0;
    background-color: #d4edda;
    padding: 10px;
    border-radius: 5px;
  }
  
  .error {
    color: red;
    font-size: 16px;
    font-weight: bold;
    margin: 12px 0;
    background-color: #f8d7da;
    padding: 10px;
    border-radius: 5px;
  }